.post {
    border-radius: 8px;
    padding: 5%;
    background-color: #2f3c40;
    width: 60%;
    text-align: center;
}

.login {
    /*resize: none;*/
    outline: none;
    color: lightslategrey;
    margin-bottom: 4%;
    width: 80%;
    height: 30px;
    border-radius: 16px;
    border: none;
    text-align: center;
    font-size: 18px;
    background-color: #606060;
}

.password {
    /*resize: none;*/
    outline: none;
    color: lightslategrey;
    margin-bottom: 4%;
    width: 80%;
    height: 30px;
    border-radius: 16px;
    border: none;
    text-align: center;
    font-size: 18px;
    background-color: #606060;
}

.checkboxDiv {
    display: flex;
    justify-content: center;
    margin-bottom: 6%;
}

.checkbox {
    cursor: pointer;
    margin-right: 10px;
}

.submit {
    color: white;
    font-size: 18px;
    width: 30%;
    height: 30px;
    border-radius: 16px;
    border: none;
    background-color: green;
    transition: 1s;
}

/* While user didn't give password or login */
.submit:disabled {
    color: lightslategrey;
    font-size: 18px;
    width: 30%;
    height: 30px;
    border-radius: 16px;
    background-color: #2f3c40;
    border: 2px solid #606060;
    transition: 1s;
}

.submit:hover {
    border: none;
    color: white;
    background-color: lightgreen;
}

.error {
    color: red;
    margin-top: 2%;
    border: 1px solid red;
    border-radius: 8px;
    padding: 5%;
}